import { Button } from "@/components/ui/button";

const appointmentPlans = [
  {
    id: "consultation-only",
    title: "Consultation Only",
    price: 1999,
    summary: "One-time consultation",
    includes: ["Doctor consultation"],
  },
  {
    id: "1-month",
    title: "1 Month Plan",
    price: 3499,
    summary: "1 month medication and support",
    includes: ["Doctor consultation", "Medication (1 month)", "Basic WhatsApp Support"],
  },
  {
    id: "3-months",
    title: "3 Months Plan",
    price: 6499,
    summary: "3 months comprehensive care",
    includes: ["Unlimited consultations", "Medication", "Priority WhatsApp Support"],
  },
  {
    id: "6-months",
    title: "6 Months Plan",
    price: 10999,
    summary: "6 months premium care",
    includes: ["Unlimited consultations", "Medication", "Premium WhatsApp Support"],
  },
];

export default function AppointmentSelector({ plans: propPlans, onSelect, selectedPlan }) {
  const displayPlans = propPlans || appointmentPlans;

  const handleSelect = (plan) => {
    onSelect(plan);
  };

  return (
    <div className="mt-6 grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
      {displayPlans.map((p) => {
        const isSelected = selectedPlan === p.id;
        return (
          <div
            key={p.id}
            className={`relative bg-white rounded-2xl shadow-md border p-6 flex flex-col transition-all ${
              isSelected ? "border-blue-500 bg-blue-50 shadow-lg" : "border-gray-200"
            }`}
          >

            {/* Title + Price */}
            <div className="flex justify-between items-center">
              <h3 className="font-semibold text-lg">{p.title}</h3>
              <span className="text-xl font-bold">₹{p.price}</span>
            </div>

            {/* Summary */}
            <p className="text-sm text-gray-600 mt-2">{p.summary}</p>

            {/* Features */}
            <ul className="mt-4 text-sm text-gray-700 space-y-2 flex-1">
              {p.includes.map((item) => (
                <li key={item} className="flex gap-2">
                  <span>✔</span> {item}
                </li>
              ))}
            </ul>

            {/* Button */}
            <Button
              className="w-full mt-5 bg-blue-600 hover:bg-blue-700 text-white"
              onClick={() => handleSelect(p)}
            >
              Add
            </Button>
          </div>
        );
      })}
    </div>
  );
}